import { fetchGoods, fetchGoodsFilters } from '../api';
import React, { useEffect, useMemo, useState } from 'react';
import { Spin, Table, Alert } from 'antd';
import moment from 'moment';
import ResizableTitle from './ResizableTitle';

const names = {
  category: '',
  brand: '',
  sales: '',
  wb_key: ''
};

const DashBoard = ({ currentWbKey, date }) => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [goods, setGoods] = useState([]);
  const [filters, setFilters] = useState({});
  const [columns, setColumns] = useState([]);
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: 10
  });

  const arrDates = useMemo(() => {
    var now = date[0].clone(),
      dates = [];

    while (now.isSameOrBefore(date[1])) {
      dates.push(now.format('YYYY-MM-DD'));
      now.add(1, 'days');
    }
    return dates;
  }, [date]);

  const handleResize =
    (index) =>
    (_, { size }) => {
      const newColumns = [...columns];
      newColumns[index] = { ...newColumns[index], width: size.width };
      setColumns(newColumns);
    };

  const mergeColumns = columns.map((col, index) => ({
    ...col,
    onHeaderCell: (column) => ({
      width: column.width,
      onResize: handleResize(index)
    })
  }));

  const getListFilters = async () => {
    try {
      setLoading(true);
      const res = await fetchGoodsFilters({
        wb_keys: currentWbKey
      });
      if (!res.detail) {
        setFilters(res);
      } else {
        setError(res.detail);
      }
    } catch (error) {
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  const getList = async (pagination, filters, sorter) => {
    let ordering;
    if (!!sorter) {
      ordering = sorter.order
        ? `${sorter.order === 'ascend' ? '' : '-'}${sorter.field}`
        : null;
    }
    try {
      setLoading(true);
      const res = await fetchGoods({
        date_from: moment(date[0]).format('YYYY-MM-DD'),
        date_to: moment(date[1]).format('YYYY-MM-DD'),
        wb_keys: currentWbKey,
        ordering,
        limit: pagination?.pageSize,
        offset: (pagination?.current - 1) * pagination?.pageSize || null,
        category: filters?.category,
        wb_key: filters?.wb_key,
        brand: filters?.brand
      });
      if (res.results) {
        const arr = [];
        res.results.forEach((item) => {
          const obj = { ...item };
          item.days.forEach((el) => {
            obj[`${el.date}`] = el.sales;
          });
          arr.push(obj);
        });

        setGoods(arr);
        setPagination((prev) => {
          return {
            ...prev,
            current: pagination?.current,
            total: Math.ceil(res.count),
            pageSize: pagination?.pageSize
          };
        });
      } else {
        setError(res.detail);
      }
    } catch (error) {
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (date) {
      getList();
    }
  }, [currentWbKey, date]);

  useEffect(() => {
    getListFilters();
  }, [currentWbKey]);

  useEffect(() => {
    setColumns([
      {
        title: names.category,
        dataIndex: 'category',
        sorter: true,
        filterSearch: true,
        width: 200,
        fixed: 'left',
        filters: filters?.categories?.map((item) => {
          return { text: item, value: item };
        })
      },
      {
        title: names.sales,
        dataIndex: 'sales',
        width: 200,
        fixed: 'left',
        sorter: true
      },
      {
        title: names.brand,
        dataIndex: 'brand',
        width: 200,
        sorter: true,
        filterSearch: true,
        filters: filters?.brands?.map((item) => {
          return { text: item, value: item };
        })
      },
      {
        title: names.wb_key,
        dataIndex: 'wb_key',
        width: 200,
        sorter: true,
        filterSearch: true,
        filters: filters?.wb_keys?.map((item) => {
          return { text: item, value: item };
        })
      },
      ...arrDates.map((day) => {
        return {
          title: moment(day).format('DD-MM-YYYY'),
          dataIndex: moment(day).format('YYYY-MM-DD'),
          width: 100
        };
      })
    ]);
  }, [filters, arrDates]);

  return (
    <>
      <Spin spinning={loading}>
        <Table
          size="small"
          scroll={{ x: 0 }}
          bordered
          components={{
            header: {
              cell: ResizableTitle
            }
          }}
          pagination={pagination}
          sticky={{ offsetHeader: 140 }}
          columns={mergeColumns}
          onChange={getList}
          dataSource={goods}
        />
      </Spin>
      {!!error && <Alert closable message={error} type="error" />}
    </>
  );
};

export default DashBoard;
